/* eslint-disable @typescript-eslint/no-unused-vars */
"use client"

import type React from "react"

import { useEffect, useState } from "react"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Edit2, Users, Search, AlertCircle, CheckCircle2, Clock, XCircle, CalendarClock, Trash2 } from "lucide-react"
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip"
import { Alert, AlertDescription } from "@/components/ui/alert"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"

interface Soutenance {
  id: number
  date: string
  time: string
  location: string
  nomGroupe: string
  juryNames: string[] | string | null
  status: string
}

interface StatusConfig {
  color: string
  bg: string
  hoverBg: string
  icon: React.ReactNode
  text: string
}

interface Salle {
  idSalle: string
  nomSalle: string
}

interface Group {
  idGroupe: string
  nomGroupe: string
  nbEtudiants: number
}

function getStatusConfig(status: string): StatusConfig {
  switch (status) {
    case "Scheduled":
      return {
        color: "text-blue-700 dark:text-blue-300",
        bg: "bg-blue-100 dark:bg-blue-900/50",
        hoverBg: "hover:bg-blue-200 dark:hover:bg-blue-900/70",
        icon: <CalendarClock className="w-3.5 h-3.5" />,
        text: "Planifié",
      }
    case "Completed":
      return {
        color: "text-green-700 dark:text-green-300",
        bg: "bg-green-100 dark:bg-green-900/50",
        hoverBg: "hover:bg-green-200 dark:hover:bg-green-900/70",
        icon: <CheckCircle2 className="w-3.5 h-3.5" />,
        text: "Terminé",
      }
    case "Pending":
      return {
        color: "text-yellow-700 dark:text-yellow-300",
        bg: "bg-yellow-100 dark:bg-yellow-900/50",
        hoverBg: "hover:bg-yellow-200 dark:hover:bg-yellow-900/70",
        icon: <Clock className="w-3.5 h-3.5" />,
        text: "En attente",
      }
    case "Cancelled":
      return {
        color: "text-red-700 dark:text-red-300",
        bg: "bg-red-100 dark:bg-red-900/50",
        hoverBg: "hover:bg-red-200 dark:hover:bg-red-900/70",
        icon: <XCircle className="w-3.5 h-3.5" />,
        text: "Annulé",
      }
    default:
      return {
        color: "text-gray-700 dark:text-gray-300",
        bg: "bg-gray-100 dark:bg-gray-800",
        hoverBg: "hover:bg-gray-200 dark:hover:bg-gray-800/70",
        icon: <AlertCircle className="w-3.5 h-3.5" />,
        text: "Inconnu",
      }
  }
}

function StatusBadge({ status }: { status: string }) {
  const config = getStatusConfig(status)

  return (
    <Badge
      variant="outline"
      className={`
        ${config.bg} 
        ${config.color} 
        ${config.hoverBg}
        border-0 
        font-medium 
        gap-1.5
        px-2.5 
        py-0.5 
        transition-colors
        duration-200
        inline-flex
        items-center
        cursor-default
        select-none
        shadow-sm
      `}
    >
      {config.icon}
      {config.text}
    </Badge>
  )
}

function DeleteDialog({
  onDelete,
  soutenanceId,
}: {
  onDelete: () => void
  soutenanceId: number
}) {
  return (
    <AlertDialog>
      <AlertDialogTrigger asChild>
        <Button variant="ghost" size="icon" className="hover:bg-red-100 hover:text-red-600 dark:hover:bg-red-900/30">
          <Trash2 className="h-4 w-4" />
          <span className="sr-only">Supprimer la soutenance</span>
        </Button>
      </AlertDialogTrigger>
      <AlertDialogContent>
        <AlertDialogHeader>
          <AlertDialogTitle>Confirmer la suppression</AlertDialogTitle>
          <AlertDialogDescription>
            Êtes-vous sûr de vouloir supprimer cette soutenance ? Cette action est irréversible.
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter>
          <AlertDialogCancel>Annuler</AlertDialogCancel>
          <AlertDialogAction onClick={onDelete} className="bg-red-600 hover:bg-red-700 dark:hover:bg-red-700">
            Supprimer
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  )
}

export default function SoutenancesPage() {
  const [soutenances, setSoutenances] = useState<Soutenance[]>([])
  const [searchTerm, setSearchTerm] = useState("")
  const [editSoutenance, setEditSoutenance] = useState<Soutenance | null>(null)
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false)
  const [groups, setGroups] = useState<Group[]>([])
  const [salles, setSalles] = useState<Salle[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const rooms: Salle[] = [
    { idSalle: "1", nomSalle: "Salle 1" },
    { idSalle: "2", nomSalle: "Salle 2" },
    { idSalle: "3", nomSalle: "Salle 3" },
    { idSalle: "4", nomSalle: "Salle 4" },
    { idSalle: "5", nomSalle: "Salle 5" },
    { idSalle: "6", nomSalle: "Salle 6" },
    { idSalle: "7", nomSalle: "Salle 7" },
    { idSalle: "8", nomSalle: "Salle 8" },
    { idSalle: "9", nomSalle: "Salle 9" },
    { idSalle: "10", nomSalle: "Salle 10" },
    { idSalle: "11", nomSalle: "Salle 11" },
    { idSalle: "12", nomSalle: "Salle 12" },
  ]

  const fetchSoutenances = async () => {
    try {
      const response = await fetch("http://localhost:5000/api/soutenance")
      const data = await response.json()
      setSoutenances(data)
    } catch (error) {
      console.error("Error fetching soutenances:", error)
    }
  }

  useEffect(() => {
    fetchSoutenances()
  }, [fetchSoutenances])

  useEffect(() => {
    if (isEditDialogOpen) {
      fetchGroupsAndSalles()
    }
  }, [isEditDialogOpen])

  const fetchGroupsAndSalles = async () => {
    try {
      setIsLoading(true)
      const groupsResponse = await fetch("http://localhost:5000/api/groups")

      if (!groupsResponse.ok) {
        throw new Error("Failed to fetch groups")
      }

      const groupsData = await groupsResponse.json()
      setGroups(groupsData)
      setSalles(rooms)
    } catch (error) {
      console.error("Error fetching data:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const handleEdit = (soutenance: Soutenance) => {
    const formattedDate = new Date(soutenance.date).toISOString().split("T")[0]
    setEditSoutenance({
      ...soutenance,
      date: formattedDate,
    })
    setIsEditDialogOpen(true)
    fetchGroupsAndSalles()
  }

  const handleSave = async () => {
    if (!editSoutenance?.id) return

    try {
      const response = await fetch(`http://localhost:5000/api/soutenance/${editSoutenance.id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(editSoutenance),
      })

      if (!response.ok) {
        throw new Error("Failed to update soutenance")
      }

      // Fetch fresh data after successful update
      await fetchSoutenances()

      // Close the dialog and reset edit state
      setIsEditDialogOpen(false)
      setEditSoutenance(null)
    } catch (error) {
      console.error("Error updating soutenance:", error)
    }
  }

  const handleCancel = () => {
    setIsEditDialogOpen(false)
    setEditSoutenance(null)
  }

  const handleDelete = async (id: number) => {
    try {
      const response = await fetch(`http://localhost:5000/api/soutenance/${id}`, {
        method: "DELETE",
      })

      if (!response.ok) throw new Error("Failed to delete soutenance")

      await fetchSoutenances()
    } catch (error) {
      console.error("Error deleting soutenance:", error)
    }
  }

  if (!soutenances || soutenances.length === 0) {
    return (
      <Card className="w-full">
        <CardContent className="pt-6">
          <Alert>
            <AlertCircle className="h-4 w-4" />
            <AlertDescription>Aucune soutenance disponible pour le moment</AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="text-2xl">Soutenances</CardTitle>
          <div className="relative w-96">
            <Search className="absolute left-2 top-2.5 h-5 w-5 text-muted-foreground" />
            <Input
              placeholder="Rechercher..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10 text-lg"
            />
          </div>
        </div>
      </CardHeader>
      <CardContent className="p-0">
        <div className="relative w-full overflow-auto">
          <Table>
            <TableHeader className="bg-muted/50">
              <TableRow>
                <TableHead className="w-[250px] font-semibold text-lg">Date</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Heure</TableHead>
                <TableHead className="w-[200px] font-semibold text-lg">Lieu</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Groupe</TableHead>
                <TableHead className="w-[300px] font-semibold text-lg">Jury</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Statut</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {soutenances
                .filter((s) => s.nomGroupe.toLowerCase().includes(searchTerm.toLowerCase()))
                .map((soutenance, index) => (
                  <TableRow
                    key={soutenance.id ?? `soutenance-${index}`}
                    className="transition-colors hover:bg-muted/50"
                  >
                    <TableCell className="font-medium text-lg">
                      {new Date(soutenance.date).toLocaleDateString("fr-FR", {
                        day: "numeric",
                        month: "long",
                        year: "numeric",
                      })}
                    </TableCell>
                    <TableCell className="text-lg">{soutenance.time}</TableCell>
                    <TableCell className="text-lg">{soutenance.location}</TableCell>
                    <TableCell>
                      <Badge variant="secondary" className="font-medium text-lg">
                        {soutenance.nomGroupe}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <TooltipProvider>
                        <Tooltip>
                          <TooltipTrigger asChild>
                            <div className="flex items-center gap-2 cursor-pointer">
                              <Users className="h-5 w-5 text-muted-foreground" />
                              <span className="truncate max-w-[250px] text-lg">
                                {Array.isArray(soutenance.juryNames)
                                  ? soutenance.juryNames.join(", ")
                                  : soutenance.juryNames}
                              </span>
                            </div>
                          </TooltipTrigger>
                          <TooltipContent>
                            <p className="max-w-xs text-lg">
                              {Array.isArray(soutenance.juryNames)
                                ? soutenance.juryNames.join(", ")
                                : soutenance.juryNames}
                            </p>
                          </TooltipContent>
                        </Tooltip>
                      </TooltipProvider>
                    </TableCell>
                    <TableCell>
                      <StatusBadge status={soutenance.status} />
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center gap-2">
                        <TooltipProvider>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <Button
                                variant="ghost"
                                size="icon"
                                className="hover:bg-muted"
                                onClick={() => handleEdit(soutenance)}
                              >
                                <Edit2 className="h-5 w-5" />
                                <span className="sr-only">Modifier la soutenance</span>
                              </Button>
                            </TooltipTrigger>
                            <TooltipContent>
                              <p className="text-lg">Modifier la soutenance</p>
                            </TooltipContent>
                          </Tooltip>
                        </TooltipProvider>

                        <TooltipProvider>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <DeleteDialog onDelete={() => handleDelete(soutenance.id)} soutenanceId={soutenance.id} />
                            </TooltipTrigger>
                            <TooltipContent>
                              <p className="text-lg">Supprimer la soutenance</p>
                            </TooltipContent>
                          </Tooltip>
                        </TooltipProvider>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </div>
      </CardContent>

      <Dialog open={isEditDialogOpen} onOpenChange={setIsEditDialogOpen}>
        <DialogContent className="sm:max-w-[800px]">
          <DialogHeader>
            <DialogTitle>Modifier la Soutenance</DialogTitle>
            <DialogDescription>Modifiez les informations de la soutenance ci-dessous.</DialogDescription>
          </DialogHeader>
          {isLoading ? (
            <div className="flex items-center justify-center p-4">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
            </div>
          ) : (
            <div className="grid gap-4 py-4">
              <div className="grid gap-2">
                <Label htmlFor="date">Date</Label>
                <Input
                  id="date"
                  type="date"
                  value={editSoutenance?.date}
                  onChange={(e) => setEditSoutenance((prev) => (prev ? { ...prev, date: e.target.value } : null))}
                />
              </div>

              <div className="grid gap-2">
                <Label htmlFor="time">Heure</Label>
                <Input
                  id="time"
                  type="time"
                  value={editSoutenance?.time}
                  onChange={(e) => setEditSoutenance((prev) => (prev ? { ...prev, time: e.target.value } : null))}
                />
              </div>

              <div className="grid gap-2">
                <Label htmlFor="group">Groupe</Label>
                <Select
                  value={editSoutenance?.nomGroupe || ""}
                  onValueChange={(groupId) => {
                    const selectedGroup = groups.find((g) => g.idGroupe === groupId)
                    if (selectedGroup && editSoutenance) {
                      setEditSoutenance({
                        ...editSoutenance,
                        nomGroupe: selectedGroup.nomGroupe,
                      })
                    }
                  }}
                >
                  <SelectTrigger id="group">
                    <SelectValue placeholder="Sélectionner un groupe" />
                  </SelectTrigger>
                  <SelectContent>
                    {groups.map((group) => (
                      <SelectItem key={`group-${group.idGroupe}`} value={group.idGroupe}>
                        {group.nomGroupe} ({group.nbEtudiants} étudiants)
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="grid gap-2">
                <Label htmlFor="jury">Jury</Label>
                <Input
                  id="jury"
                  type="text"
                  value={editSoutenance?.juryNames || ""}
                  onChange={(e) => setEditSoutenance((prev) => (prev ? { ...prev, juryNames: e.target.value } : null))}
                />
              </div>

              <div className="grid gap-2">
                <Label htmlFor="salle">Salle</Label>
                <Select
                  value={editSoutenance?.location}
                  onValueChange={(value) => setEditSoutenance((prev) => (prev ? { ...prev, location: value } : null))}
                >
                  <SelectTrigger id="salle">
                    <SelectValue placeholder="Sélectionner une salle" />
                  </SelectTrigger>
                  <SelectContent>
                    {Array.from({ length: 12 }, (_, i) => (
                      <SelectItem key={i + 1} value={`Salle ${i + 1}`}>
                        Salle {i + 1}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="grid gap-2">
                <Label htmlFor="status">Statut</Label>
                <Select
                  value={editSoutenance?.status}
                  onValueChange={(value) => setEditSoutenance((prev) => (prev ? { ...prev, status: value } : null))}
                >
                  <SelectTrigger id="status">
                    <SelectValue placeholder="Sélectionner un statut" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Scheduled">Planifié</SelectItem>
                    <SelectItem value="Completed">Terminé</SelectItem>
                    <SelectItem value="Pending">En attente</SelectItem>
                    <SelectItem value="Cancelled">Annulé</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          )}
          <div className="flex justify-end gap-4">
            <Button variant="outline" onClick={handleCancel}>
              Annuler
            </Button>
            <Button onClick={handleSave} disabled={isLoading}>
              Sauvegarder
            </Button>
          </div>
        </DialogContent>
      </Dialog>
    </Card>
  )
}

